"use client";

import SearchBox from "@/components/SearchBox";
import AnimeList from "@/components/AnimeList";
import { useAnimeList } from "@/hooks/useAnimeList";

export default function Page() {
  const { items, search, sortItems, loadMore, finished, loading } = useAnimeList();

  return (
    <div
      style={{
        minHeight: "100vh",
        backgroundImage: "url('/scenario.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        padding: 20,
        display: "flex",
        justifyContent: "center",
        alignItems: "flex-start",
      }}
    >
      <div
        style={{
          width: "100%",
          maxWidth: 600,
          backgroundColor: "rgba(0,0,0,0.7)",
          padding: 20,
          borderRadius: 12,
          color: "white",
          boxShadow: "0 4px 12px rgba(0,0,0,0.5)",
        }}
      >
        <h1 style={{ marginBottom: 20, textAlign: "center", fontSize: 28 }}>
          Buscar Animes
        </h1>

        <SearchBox onSearch={search} />

        <div style={{ display: "flex", gap: 10, marginBottom: 20 }}>
          <button
            onClick={() => sortItems("nome")}
            style={{
              flex: 1,
              padding: "10px 0",
              borderRadius: 6,
              border: "none",
              cursor: "pointer",
              backgroundColor: "#1e88e5",
              color: "white",
              fontWeight: "bold",
            }}
          >
            Ordenar por Nome
          </button>

          <button
            onClick={() => sortItems("ano")}
            style={{
              flex: 1,
              padding: "10px 0",
              borderRadius: 6,
              border: "none",
              cursor: "pointer",
              backgroundColor: "#43a047",
              color: "white",
              fontWeight: "bold",
            }}
          >
            Ordenar por Ano
          </button>
        </div>

        <AnimeList items={items} loadMore={loadMore} finished={finished} />

        {loading && <p style={{ textAlign: "center", marginTop: 10 }}>Carregando...</p>}
      </div>
    </div>
  );
}
